export class TipoComprobante {
  codigo: string;
  descripcion: string;
  ruta: string;

  constructor(codigo: string, descripcion: string, ruta: string) {
    this.codigo = codigo;
    this.descripcion = descripcion;
    this.ruta = ruta;
  }
}

export class TiposComprobantes {
  tiposComprobantes: TipoComprobante[];
  constructor() {
    this.tiposComprobantes = [
      new TipoComprobante('01', 'Factura', 'factura'),
      new TipoComprobante('03', 'Boleta', 'boleta'),
      new TipoComprobante('07', 'Nota de Crédito', 'notaCredito'),
      new TipoComprobante('08', 'Nota de Débito', 'notaDebito')
    ];
  }

  get(codigo: string) {
    return this.tiposComprobantes.find(item => item.codigo === codigo);
  }

  getFactura() {
    return this.get('01');
  }

  getBoleta() {
    return this.get('03');
  }

  getNotaCredito() {
    return this.get('07');
  }

  getNotaDebito() {
    return this.get('08');
  }
};



