import {Component, OnInit} from '@angular/core';
import {TipoComprobante, TiposComprobantes} from './modelos/tipos-comprobantes';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'app-comprobantes',
  templateUrl: './comprobantes.component.html',
  styleUrls: ['./comprobantes.component.css']
})
export class ComprobantesComponent implements OnInit {
  titulo = 'Crear Comprobante';
  tiposComprobante = new TiposComprobantes();
  comprobanteSeleccionado: TipoComprobante;

  constructor(private router: Router,
              private route: ActivatedRoute) { }

  ngOnInit() {
    this.comprobanteSeleccionado = this.tiposComprobante.getFactura();
    this.navegarHacia(this.comprobanteSeleccionado);
  }

  cargarComprobante() {
    this.navegarHacia(this.comprobanteSeleccionado);
  }

  navegarHacia(tipoComprobante: TipoComprobante) {
    const ruta =  tipoComprobante.ruta;
    this.router.navigate([ruta + '/crear'], {relativeTo: this.route});
  }

}



